     /**
     * 
     * @param {*} formdata 
     */
    systemSaveFun(formdata){
        let requestParam = {
            model: formdata,
            // pageid: pageCode.sysFormPageCode//pageid :    jsoncode
            pageid:this.props.nodePageCode
        };
        let that = this;
        let saveurl= ajaxUrl.sysSaveUrl;
        if(sysSaveType==='copy'){
            saveurl= ajaxUrl.sysCopySaveUrl; 
        }
        ajax({
            url:saveurl,
            method:"post",
            data:{form:requestParam,nodeType:this.props.nodeType,pk_svid:sysVRefPk},
            success:function(res){
                if(res.success){
                    let treeData = that.dealTreeData(res.data);
                    if(sysSaveType==='add'||sysSaveType==='copy'){//
                        that.props.syncTree.addNodeSuccess(leftTree,treeData);
                    }else{//
                        that.props.syncTree.editNodeSuccess(leftTree, treeData);
                    }
                    toast({ color: 'success', title: that.state.json['10100BOSB-000002'] });/*  */
                    that.props.modal.close(sysModalId);
                    that.windowCloseListener('browse');
                    that = null;
                }else{
                    toast({content : res.message,color : 'warning'});
                }
            },
            error: function(res){
                toast({content : res.message,color : 'warning'});
            }
        });
    }
    
    /**
     * 
     * @param data
     * @returns {*}
     */
    dealTreeData(data){
        let deleteDataChildrenProp = function(node){
            node.iconBox = {
                editIcon: true,
                addIcon: true,
                delIcon: true
            }
            if(!node.children || node.children.length == 0) {
                delete node.children;
            }
            else{
                node.isLeaf = false;
                node.children.forEach( (e) => {
                    deleteDataChildrenProp(e);
                } );
            }
        };
        data.forEach( (e) => {
            e.iconBox = {
                editIcon: true,
                addIcon: true,
                delIcon: true
            }
            deleteDataChildrenProp(e);
        });
        return data;
    }
