    onClickButton(props,id){
        switch (id) {
            case 'Add':
                this.onAdd();
                break;
            case 'Copy':
                this.onCopy();
                break;	
            case 'Refresh':
                this.loadLeftTreeData(isShowDisable,true);
                break;
            case 'Enable':
                promptBox({
                    color: 'warning',               // "success" "success"/"info"/"warning"/"danger",
                    title:this.state.json['10100BOSB-000033'],/*  */
                    content:this.state.json['10100BOSB-000034'],/*  */
                    beSureBtnClick:()=>{this.onEnable()}
                })
                break;
            case 'Disable':
                promptBox({
                    color: 'warning',               // "success" "success"/"info"/"warning"/"danger",
                    title:this.state.json['10100BOSB-000033'],/*  */
                    content:this.state.json['10100BOSB-000035'],/*  */
                    beSureBtnClick: ()=>{this.onDisable()}
                })
                break;
            case 'Import':
                this.onImportClick();
                break;
            case 'AddVirtual':
                this.onAddMember();
                break;
            case 'SortManage':
                this.onSortClick();
                break;
            case 'Version':
                this.onCreatVersion();
                break;
            case 'AddStatMemb':
                this.onAddStatMember();
                break;
            case 'Print':
                let allD = this.props.syncTree.getSyncTreeValue(leftTree);
                let pks = [];
                allD.forEach((item,index) => {
                    pks.push(item.id);
                });
                print(
                    'pdf',  //: 'html', 'pdf'pdf
                    ajaxUrl.printUrl, 
                    {
                        //billtype:'',  //
                        funcode: props.appCode,      //
                        nodekey:'budgetorgstruprint',     //
                        oids: pks    //['1001A41000000000A9LR','1001A410000000009JDD']  pk  oids,
                    }
                );
                break;
            case 'Output':
                let allD1 = this.props.syncTree.getSyncTreeValue(leftTree);
                let pks1 = [];
                allD1.forEach((item,index) => {
                    pks1.push(item.id);
                });
                this.setState({
                    pks: pks1
                 },this.refs.printOutput.open());
                // print(
                //     'pdf',  //: 'html', 'pdf'pdf
                //     ajaxUrl.printUrl, 
                //     {
                //         //billtype:'',  //
                //         funcode: props.appCode,      //
                //         //nodekey:'',     //
                //         // nodekey:'assprinttem',  
                //         outputType:'output',
                //         oids: pks1    //['1001A41000000000A9LR','1001A410000000009JDD']  pk  oids,
                //     }
                // );
                break;
            case 'AddVersion':
                this.onAddVersion();
                break;
            case 'EditVersion':
                this.onEditVersion();
                break;
            case  'DelVersion':
                this.onDelVersion();
                break;
            case 'RefreshVersion':
                // this.onRefreshVersion();
                this.onCreatVersion('Refresh');
                // this.props.button.setButtonDisabled('DelVersion',true);
                break;
            case 'CancelVersion':
                promptBox({
                    color: 'warning',               // "success" "success"/"info"/"warning"/"danger",
                    title:this.state.json['10100BOSB-000010'],/*  */
                    content:this.state.json['10100BOSB-000011'],/*  */
                    beSureBtnClick:()=>{
                        this.onRefreshVersion();
                        this.windowCloseListener('browse');
                    }
                })
                break;
            case 'SaveVersion':
                this.onVersionSave('SaveVersion');
                break;
            default:
                break;
        }
    }
